.scanning-description {
  margin-right: 0;

  @media screen and (min-width: 768px) {
    // margin-top: 44px;
    background-image: url(../images/tablet/3d-scanning/scan-bcg-tablet@1x.png);
    background-repeat: no-repeat;
    background-position: bottom right;
    background-size: 241px;
  }

  @media (min-device-pixel-ratio: 2) and (min-width: 768px),
    (min-resolution: 192dpi) and (min-width: 768px),
    (min-resolution: 2dppx) and (min-width: 768px) {
    background-position: bottom right;
    background-size: 241px;
    background-image: url(../images/tablet/3d-scanning/scan-bcg-tablet@2x.png);
  }

  @media screen and (min-width: 1440px) {
    padding-bottom: 100px;
    // margin-top: 87px;
    background-image: url(../images/desktop/3d-scanning/scan-bcg-desktop@1x.png);
    background-repeat: no-repeat;
    background-size: 515px;
    background-position: 100% 90px;
  }

  @media (min-device-pixel-ratio: 2) and (min-width: 1440px),
    (min-resolution: 192dpi) and (min-width: 1440px),
    (min-resolution: 2dppx) and (min-width: 1440px) {
    padding-bottom: 100px;
    background-image: url(../images/desktop/3d-scanning/scan-bcg-desktop@2x.png);
  }
}

.scanning-background {
  @media screen and (max-width: 767px) {
    display: inline-flex;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    height: 200px;
    margin-top: 16px;
    background-image: url(../images/mobile/3d-scanning/scan-bcg-mobile@1x.png);
  }

  @media (min-device-pixel-ratio: 2) and (max-width: 767px),
    (min-resolution: 192dpi) and (max-width: 767px),
    (min-resolution: 2dppx) and (max-width: 767px) {
    background-image: url(../images/mobile/3d-scanning/scan-bcg-mobile@2x.png);
  }
}

.scanning-process {
  margin-top: 32px;

  @media screen and (min-width: 768px) {
    margin-top: 52px;
  }
  @media screen and (min-width: 1440px) {
    margin-top: 0;
  }
}

.scanning-process_list {
  display: flex;
  text-align: center;
  margin-left: -15px;
  margin-top: -0px;

  @media screen and (max-width: 767px) {
    flex-wrap: wrap;

    & .scanning-process_item:nth-child(2n) {
      margin-left: 15px;
    }
  }

  @media screen and (max-width: 768px) {
    margin-top: 24px;
  }

  @media screen and (min-width: 768px) {
    & .scanning-process_item {
      margin-left: 12px;
    }
  }

  & .scanning-process_item {
    padding: 15px 10px 15px 10px;
    margin-top: 16px;
    box-shadow: inset 1px 1px 16px #cbcbcb, inset -1px -1px 16px #ffffff;
    border-radius: 50px 0px;

    @media screen and (max-width: 767px) {
      flex-basis: calc((100% - 60px) / 2);
      margin-left: 15px;
      text-align: center;
      justify-content: center;
    }
  }

  & .scanning-process_item:nth-child(1):before {
    content: "";
    display: inline-block;
    width: 94px;
    height: 82px;
    background-image: url(/images/mobile/3d-scanning/scan1-mobile@1x.png);
    background-repeat: no-repeat;
    background-size: contain;
  }
  & .scanning-process_item:nth-child(2):before {
