.scanning-description_title {
  @include title-main($fs: 24px, $lh: 30px);

  @media screen and (min-width: 768px) {
    @include title-main ($fs: 36px, $lh: 45px);
  }

  @media screen and  (min-width: 1440px) {
    @include title-main ($fs: 48px, $lh: 60px);
  }
}
.scanning-description_list {
  @media screen and (min-width: 1440px) {
    & .scanning-description_item:last-child {
      margin-left: 32px;
    }
    display: flex;
  }

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.scanning-description_text {
  @include txt($fs: 16px, $lh: 20px);
  margin-top: 16px;
  line-height: 1.25;

  @media screen and (min-width: 768px) {
    width: 514px;
  }

  @media screen and (min-width: 1440px) {
    font-size: 18px;
    line-height: 1.27;
  }
}

.scanning-stage_description {
  font: var(--font-main);
  text-align: center;
  margin-top: 18px;
  @include txt($fs: 14px, $fw: 400, $lh: 17px, $color: var(--color-accent));

  @media screen and (min-width: 1440px) {
    @include txt($fs: 18px, $fw: 300, $lh: 23px, $color: var(--color-accent));
  }
}

